import React, { useState, useEffect } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Generator from './Generator';
import WizardGenerator from './WizardGenerator';

import './dataMart.scss';



const DataMart = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Generator')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
        path: path === '/data-mart-generator/generator' ? null : '/data-mart-generator/generator',
        label: 'DATA MART GENERATOR'
    },
    {
        label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/data-mart-generator/generator',
      label: currentPath === 'wizard-generator' ? null : 'Generator'
    },
    {
      path: '/data-mart-generator/wizard-generator',
      label: currentPath === 'generator' ? null : 'Wizard Generator'
    }
  ]

  return (
    <div className="data-mart-page">
      <PageHeader 
        title="DATA MART REPORT GENERATOR" 
        color="turquoise" 
        innerPage={innerPage} 
        breadcrumbs={breadcrumbs}
        previousPage="/"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'generator' && <Generator/>}
        {currentPath === 'wizard-generator' && <WizardGenerator/>}
      </div>
    </div>
  );
}

export default DataMart;
