import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

const GeneralInfoContent = (props) => {
  const { generalInfoData } = props;

  return (
    <Container className="overview" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <Col xl={5}>
          <Card>
            <Card.Body>
              <Card.Title>overview</Card.Title>
              {generalInfoData.overview.summary}
              {generalInfoData.overview.details && generalInfoData.overview.details.map((item) => {
                return (
                  <div>
                    {item.label}
                    <span>
                      {item.value}
                    </span>
                  </div>
                )
              })}
            </Card.Body>
          </Card>
        </Col>
        <Col xl={7}>
          <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
            <Col xl={6}>
              <Card>
                <Card.Body>
                  <Card.Title>key people</Card.Title>

                </Card.Body>
              </Card>
            </Col>
            <Col xl={6}>
              <Card>
                <Card.Body>
                  <Card.Title>relationship with equinix</Card.Title>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className="contentDivider">
            <Col xl={6}>
              <Card>
                <Card.Body>
                  <Card.Title>locations</Card.Title>
                </Card.Body>
              </Card>
            </Col>
            <Col xl={6}>
              <Card>
                <Card.Body>
                  <Card.Title>relevant news</Card.Title>
                </Card.Body>
              </Card>
            </Col>
          </Row>

        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <Col xl={6}>
          <Card>
            <Card.Body>
              <Card.Title>data center hosting products</Card.Title>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={6}>
          <Card>
            <Card.Body>
              <Card.Title>cloud hosting products</Card.Title>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <Col xl={6}>
          <Card>
            <Card.Body>
              <Card.Title>cloud security products</Card.Title>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={6}>
          <Card>
            <Card.Body>
              <Card.Title>content delivery products</Card.Title>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <Col xl={6}>
          <Card>
            <Card.Body>
              <Card.Title>dns products</Card.Title>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={6}>
          <Card>
            <Card.Body>
              <Card.Title>traffic management products</Card.Title>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default GeneralInfoContent;